@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{margin:0;padding:0;list-style:none;vertical-align:baseline}div{position:relative;z-index:2}body{background-color:#333;color:#333;font-family:'Raleway', sans-serif;font-weight:400;display:flex;justify-content:center;align-items:center;height:100vh}.footer{width:450px;text-align:center;display:block;margin:15px auto 30px auto;font-size:0.8em;color:#001b44}.footer a{font-weight:500;text-decoration:none;color:#001b44}#quote-box{border-radius:3px;position:relative;width:550px;padding:40px 50px;display:table;background-color:#fff}#quote-box .quote-text{text-align:center;width:450px;height:auto;clear:both;font-weight:500;font-size:1.75em}#quote-box .quote-text i{font-size:1.0em;margin-right:0.4em}#quote-box .quote-author{width:450px;height:auto;clear:both;padding-top:20px;font-size:1em;text-align:right}#quote-box .buttons{width:450px;margin:auto;display:block}#quote-box .buttons .button{height:38px;border:none;border-radius:3px;color:#fff;background-color:#001b44;outline:none;font-size:0.85em;padding:8px 18px 6px 18px;margin-top:30px;opacity:1;cursor:pointer}#quote-box .buttons .button:hover{opacity:0.9}#quote-box .buttons .button#twitter-quote,#quote-box .buttons .button#tumblr-quote{float:left;padding:0px;padding-top:8px;text-align:center;font-size:1.2em;margin-right:5px;height:30px;width:40px}#quote-box .buttons .button#new-quote{float:right}#quote-box .buttons .button .icon{margin:-6px auto 10px auto;color:azure}

